	module.exports = {"data":{"markdownRemark":{"html":"<h2 id=\"observe-what-is-running\"><a href=\"#observe-what-is-running\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Observe what is running</h2>\n<p>We augment the application status data that OpenShift already keeps by regularly inspecting the master API and the management interface (part of our runtime contract) of the applications. The extra status data collected is compiled into a separate status value called <a href=\"/aurora/documentation/openshift/#application-monitoring\">AuroraStatus</a>. This allow us, among other things, to create custom wallboards, alert integrations, rate of errors and 95% percentile response times.</p>","headings":[{"value":"Observe what is running","depth":2}],"fields":{"slug":"/frontpage/observe/"},"frontmatter":{"title":""}}},"pathContext":{"slug":"/frontpage/observe/"}}

